<script lang="ts">
  import { analytics } from "@lib/analytics"
  import { Paths, routerPath, useRouter } from "@lib/router"
  import * as SCA from "@lib/sca"
  import { skinCare } from "@pages/best-match/stores"
  import { setSourceChanged } from "@pages/gift"
  import imageSource from "../best-match/stores/image-source"
  import AnalysisProgress from "./AnalysisProgress.svelte"
  import CameraFrameCapture from "./CameraFrameCapture.svelte"
  import { percent, result, step } from "./state"

  const router = useRouter()

  let screen: typeof CameraFrameCapture | typeof AnalysisProgress

  $: {
    if (!$imageSource) break $
    if ($imageSource.type === "photo") screen = AnalysisProgress
    else if ($imageSource.type === "webcam" && $imageSource.data) screen = AnalysisProgress
    else screen = CameraFrameCapture
  }
  $: {
    if (!$imageSource) break $
    if ($imageSource.type === "photo") analyze($imageSource.data)
    else if ($imageSource.type === "webcam" && $imageSource.data) analyze($imageSource.data)
  }

  const analyze = async (photo: Blob) => {
    analytics.face_analysis_started()

    $result = await SCA.analyze(photo, (progress) => {
      $percent = progress.percent
      $step = progress.step
    })

    skinCare.resetQuiz()
    analytics.face_analysis_finished()

    setSourceChanged(false)
  }
</script>

<svelte:component
  this={screen}
  intro={$imageSource?.type === "webcam"}
  on:photo={({ detail }) => $imageSource && ($imageSource.data = detail)}
  on:ended={() => {
    if ($routerPath.current === Paths.SkinCare) router.push(Paths.SkinCare)
    else router.push(Paths.BestMatch)
  }}
/>
